export default {
	data() {
		return {
			homeData: [],
			province: [],
			cities:[],
			abroadData:[],
			limit: -1,
			flag:'∨'
		};
	},
	onLoad() {
		uni.showLoading({  
		    title: '加载中'  
		});
		this.getListByCountryTypeService1();
		this.getListByCountryTypeService2();
	},
	onPullDownRefresh() {
		this.getListByCountryTypeService1();
		this.getListByCountryTypeService2();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		getListByCountryTypeService1() {
			const self = this;
			uni.request({
				url: self.$baseUrl + '/data/getListByCountryTypeService1', //获取首页数据接口。
				data: {},
				header: {},
				success: res => {
					uni.hideLoading(); 
					self.homeData = res.data;
					
				},
				fail: () => {
					uni.showToast({
						title: '网络请求失败！',
						duration: 1500
					});
				}
			});
		},
		getListByCountryTypeService2(){
			const self = this;
			uni.request({
				url: self.$baseUrl + '/data/getListByCountryTypeService2', //获取首页数据接口。
				data: {},
				header: {},
				success: res => {
					uni.hideLoading(); 
					self.abroadData = res.data;
					
				},
				fail: () => {
					uni.showToast({
						title: '网络请求失败！',
						duration: 1500
					});
				}
			});
		},
		getAreaStat(province) {
			const self = this;
			uni.request({
				url: self.$baseUrl + '/data/getAreaStat/' + province, //获取首页数据接口。
				data: {},
				header: {},
				success: res => {
					self.province = res.data;
					self.cities = self.province[0].cities;
					console.log(self.province);
				},
				fail: () => {
					uni.showToast({
						title: '网络请求失败！',
						duration: 1500
					});
				}
			});
		},
		showInfo(provinceName, index) {
			this.getAreaStat(provinceName);
			if (index == this.limit) {
				this.limit = -1;
				// this.flag = '∨'
			} else {
				this.limit = index;
				// this.flag = '∧'
			}
		}
	}
};
